.movieName,
.genres {
    text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	width: 210px;
}

.movieName {
	font-family: "HelveticaNeue-Bold", sans-serif;
	font-size: 20px;
	text-transform: uppercase;
	font-weight: 700;
	margin-bottom: 5px;
}

.genres {
	font-family: "HelveticaNeue-Medium", sans-serif;
	font-weight: 500;
	color: #47c3f0;
}