.playButton {
	cursor: pointer;
	position: relative;
	color: #fff;
	font-size: 40px;
	border-radius: 50%;
	padding: 30px 40px;
	background-color: #3bb4e0;
	margin-bottom: 10px;
	border: none;
	transition: all 0.2s;
}

.playButton:hover {
	transform: scale(1.05);
}

.playIcon {
	position: relative;
	left: 5px;
}

.playButtonSign {
	font-size: 20px;
	user-select: none;
}