.searchWrapper {
    display: flex;
    align-items: center;
    border: 2px solid white;
    border-radius: 6px;
}

.searchField {
    font-family: sans-serif;
    background-color: transparent;
    padding: 11.5px 10px;
    margin: 0 !important;
    border: none;
    width: 150px;
    margin-right: 10px;
    transition: all 0.3s;
    color: #fff;
    font-size: 17px;
}

.searchField:focus  {
    background-color: #fff;
    color: #000;
}

.searchField::placeholder {
    color: #fff;
}

.searchIcon {
    cursor: pointer;
    color: #fff;
    font-size: 25px;
    padding: 9px 10px 9px 0;
}