.dropdown {
	position: absolute;
	right: -1px;
	top: 30px;
	background-color: #fff;
	border-radius: 5px;
	text-align: left;
}

.button {
	cursor: pointer;
	white-space: nowrap;
	color: #000;
	padding: 10px 30px 10px 10px;
	text-align: left;
}

.button:hover {
	background-color: rgba(0, 0, 0, 0.1);
}

.icon {
	color: rgba(0, 0, 0, 0.65);
	margin-right: 5px;
}
