@font-face {
	font-family: "HelveticaNeue-Thin.ttf";
	src: url(./fonts/HelveticaNeue-Thin.ttf) format("truetype");
}

@font-face {
	font-family: "HelveticaNeue-Regular.ttf";
	src: url(./fonts/HelveticaNeue-Regular.ttf) format("truetype");
}

@font-face {
	font-family: "HelveticaNeue-Medium.ttf";
	src: url(./fonts/HelveticaNeue-Medium.ttf) format("truetype");
}

@font-face {
	font-family: "HelveticaNeue-Bold.ttf";
	src: url(./fonts/HelveticaNeue-Bold.ttf) format("truetype");
}

@font-face {
	font-family: "HelveticaNeue-CondensedBlack.ttf";
	src: url(./fonts/HelveticaNeue-CondensedBlack.ttf) format("truetype");
}

::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
	background-color: transparent;
}

::-webkit-scrollbar-thumb {
	background-color: rgba(0,0,0,0.2);
	border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
	background-color: rgba(0,0,0,0.3);
}

::-webkit-scrollbar-thumb:active {
	background-color: rgba(0,0,0, 0.5);
}

::-webkit-scrollbar-thumb:vertical {
	min-height: 20px;
}

::-webkit-scrollbar-thumb:horizontal {
	min-width: 20px;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
