.card {
	position: relative;
	overflow: hidden;
	margin: 5px;
	transition: all 0.5s;
}

@media (min-width: 800px) {
	.card {
		margin: 15px 10px;
	}
}

@media (min-width: 1200px) {
	.card {
		margin: 30px 20px;
	}
}

.shortDescription {
	display: flex;
	justify-content: space-between;
	background-color: #fff;
	border-radius: 0 0 5px 5px;
	padding: 35px 15px;
	height: 40px;
	transition: all 1s;
}

.picture {
	display: block;
	border-radius: 5px 5px 0 0;
	width: 300px;
	height: 300px;
	object-fit: cover;
	transition: all 1s;
}

.overlay,
.infoOverlay {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	transition: all 0.4s ease;
	background-color: rgba(0, 0, 0, 0.5);
	color: #fff;
	border-radius: 20px;
	text-align: center;
}

.overlay {
	justify-content: center;
	align-items: center;
	opacity: 0;
	text-transform: capitalize;
}

.overlayContent {
	transition: all 1s ease;
}

.imageWrapper {
	position: relative;
}

.imageWrapper:hover .overlay {
	opacity: 1;
}

.infoTopButtons {
	display: flex;
	justify-content: space-between;
	padding: 20px;
}

.infoClose {
	cursor: pointer;
	font-size: 20px;
	font-weight: 400;
}

.infoShortDescription {
	padding: 0 20px;
	display: flex;
	justify-content: space-between;
	text-align: left;
}

.infoDescription {
	padding: 15px 20px;
	text-align: left;
	font-size: 19px;
}