.button {
	cursor: pointer;
	background-color: transparent;
	border: 2px solid #fff;
	color: #fff;
	font-size: 20px;
	border-radius: 6px;
	padding: 10px 30px;
	text-transform: capitalize;
	transition: all 0.3s;
}

.white:hover {
	background-color: #fff;
	color: #000;
}

.blue {
	border: none;
	background-color: #3bb4e0;
	padding: 12px 30px;
}

.transparent {
	border: 2px solid transparent;
}

.transparent:hover {
	border: 2px solid #fff;	
}